
.explore-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.explore-title {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Coming Soon Section */
.coming-soon-container {
  background-color: #f8f8f8;
  padding: 40px;
  border-radius: 8px;
  margin: 40px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.coming-soon-title {
  font-size: 30px;
  color: #333;
  margin-bottom: 10px;
}

.coming-soon-text {
  font-size: 20px;
  color: blue;
  line-height: 1.8;
  font-weight: 400;
  max-width: 700px;
  margin: 0 auto;

}
