/* Main Career Page Container */
.career-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 20px;
  
  color: white;
  overflow-y: auto; 
}

/* Header */
.career-header {
  margin-bottom: 20px;
}

.career-header h1 {
  font-size: 1.5rem;
}

.career-header p {
  font-size: 1rem;
  color: #a1a1a6;
}

/* Career Categories */
.career-categories {
  flex-grow: 1;
}

.career-category {
  margin-bottom: 20px;
}

.career-category h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.career-job-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.job-button {
  background-color: #2c2c2e;
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.job-button:hover {
  background-color: #3a3a3c;
}

/* Bottom Navigation */
.bottom-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #2c2c2e;
  padding: 15px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #8e8e93;
}

.nav-item.active {
  color: white;
}

.nav-item span {
  font-size: 1.5rem;
}

.nav-item p {
  margin: 0;
  font-size: 0.75rem;
}
